:root {
  --header-h: 75px;
  --aside-h: 100vh;
  --aside-w: 270px;
  /* Color */
  --clr-black: #202020;
  --clr-white: #fff;
  --clr-purple: 107, 36, 240;
  /* --clr-lightblue: 14, 165, 233; */
  --clr-lightblue: 99, 102, 241;
  /* --clr-blue: 2,132,199; */
  --clr-blue: 99, 102, 241;
  --clr-lightgray: 243, 244, 246;
  --clr-gray: 156, 163, 175;
  --clr-darkgray: 31, 41, 55;
  /* Font Family */
  --font-osw: 'Oswald', sans-serif;
  --font-pops: 'Poppins', sans-serif;
  --font-robt: 'Roboto', sans-serif;
  /* Radius */
  --radius-circle: 50%;
  --radius-sm: 5px;
  --radius-md: 10px;
  --radius-lg: 20px;
}

* {
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-robt);
}

/* ================= Input Number remove Arrow ================= */

/* Chrome, Safari, Edge, Opera */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

input[type=number] {
  -moz-appearance: textfield;
}

/* ================= Scrollbar ================= */

/* width */

.aside-container::-webkit-scrollbar {
  width: 5px;
}

/* Track */

.aside-container::-webkit-scrollbar-track {
  background: #fff;
}

/* Handle */

.aside-container::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background: rgba(99, 102, 241, 50%);
  background: rgba(var(--clr-lightblue), 50%);
}

/* Handle on hover */

.aside-container::-webkit-scrollbar-thumb:hover {
  background: rgba(99, 102, 241, 70%);
  background: rgba(var(--clr-lightblue), 70%);
}

/* ================= Main ================= */

.main-content {
  width: 100%;
  position: relative;
  margin-top: 75px;
  margin-top: var(--header-h);
  z-index: 1;
}

.main-bg::after {
  content: '';
  width: 100%;
  position: absolute;
  top: 0;
  height: 20vh;
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-lightblue), 1);
  box-shadow: inset 0px -10px 10px rgba(0, 0, 0, 20%);
  z-index: -99;
}

.main-content > * {
  box-sizing: border-box;
}

/* ================= Aside ================= */

.aside-container {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 20%);
}

.menu-list {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.menu-group {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  grid-gap: 3px;
  gap: 3px;
}

.menu-group .menu-item, .menu-group .menu-item-parent {
  margin-left: 10px;
}

.menu-item-parent {
  position: relative;
  display: flex;
  border-radius: 10px;
  padding: 10px;
}

.menu-item-parent:hover {
  background-color: rgba(99, 102, 241, 10%);
  background-color: rgba(var(--clr-lightblue), 10%);
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
  cursor: pointer;
}

.menu-item-parent.active {
  background-color: rgba(99, 102, 241, 10%);
  background-color: rgba(var(--clr-lightblue), 10%);
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
}

.menu-item {
  display: flex;
  border-radius: 10px;
  padding: 10px;
}

.menu-item:hover {
  background-color: rgba(99, 102, 241, 10%);
  background-color: rgba(var(--clr-lightblue), 10%);
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
  cursor: pointer;
}

.menu-item.active {
  background-color: rgba(99, 102, 241, 10%);
  background-color: rgba(var(--clr-lightblue), 10%);
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
}

.menu-dropdown-group .arrow-down-icon {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%) rotate(-180deg);
  font-size: 30px;
  transition: .1s;
}

.menu-dropdown-group.active .arrow-down-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.menu-dropdown-group.active .menu-dropdown-child {
  max-height: 0;
  transition: max-height 0.5s ease-out;
}

.menu-dropdown-child {
  position: relative;
  margin-left: 20px;
  max-height: 500px;
  overflow: hidden;
  transition: max-height 0.5s ease-in;
}

.menu-item-child {
  display: flex;
  border-radius: 10px;
  padding: 10px;
}

.menu-item-child:hover {
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
  cursor: pointer;
}

.menu-item-child.active {
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
}

.menu-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}

.menu-title {
  display: flex;
  align-items: center;
  flex: 3 1;
}

.menu-card {
  display: block;
  position: relative;
  margin: 20px 0 20px 10px;
  height: 130px;
  padding: 10px;
  background-color: rgba(99, 102, 241, 20%);
  background-color: rgba(var(--clr-lightblue), 20%);
  border-radius: 10px;
  box-sizing: border-box;
  overflow: hidden;
}

.menu-card::before {
  content: '';
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: absolute;
  top: -35px;
  right: -45px;
  background-color: rgba(99, 102, 241, 50%);
  background-color: rgba(var(--clr-lightblue), 50%);
}

.menu-card::after {
  content: '';
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: absolute;
  bottom: -50px;
  left: -40px;
  border: 5px solid rgba(99, 102, 241, 50%);
  border: 5px solid rgba(var(--clr-lightblue), 50%);
}

.menu-card .card-button {
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-lightblue), 1);
  color: #fff;
  color: var(--clr-white);
}

.menu-card .card-button:hover {
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-blue), 1);
  cursor: pointer;
}

.aside-brand {
  width: 100%;
  height: 75px;
  height: var(--header-h);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Oswald', sans-serif;
  font-family: var(--font-osw);
}

.logo-img {
  width: 60%;
}

/* ================= Header ================= */

.aside-button {
  padding: 3px;
  border: 2px solid rgba(99, 102, 241, 1);
  border: 2px solid rgba(var(--clr-lightblue), 1);
  border-radius: 10px;
}

.aside-button:hover {
  cursor: pointer;
}

.aside-toggle-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 2px;
  gap: 2px;
  padding: 3px;
  height: 35px;
  border-radius: 5px;
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-lightblue), 1);
}

.aside-toggle-icon:hover {
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-blue), 1);
}

.header-right-side-wrapper {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
}

.search-bar {
  display: flex;
  grid-gap: 2px;
  gap: 2px;
  padding: 3px;
  height: 45px;
  border: 2px solid rgba(99, 102, 241, 1);
  border: 2px solid rgba(var(--clr-lightblue), 1);
  background-color: rgba(156, 163, 175, 10%);
  background-color: rgba(var(--clr-gray), 10%);
  border-radius: 10px;
}

.search-bar-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-lightblue), 1);
  border-radius: 5px;
}

.search-bar-icon:hover {
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-blue), 1);
  cursor: pointer;
}

.profile-bar {
  position: relative;
  padding: 3px;
  width: 50px;
  height: 50px;
  border: 2px solid rgba(99, 102, 241, 1);
  border: 2px solid rgba(var(--clr-lightblue), 1);
  border-radius: 50%;
  box-sizing: border-box;
}

.profile-bar:hover {
  cursor: pointer;
}

.profile-bar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.popover-profile {
  opacity: 0;
  visibility: hidden;
  transition: .3s;
  position: absolute;
  top: 110%;
  right: 0;
  padding: 20px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 50%);
  background-color: #fff;
  background-color: var(--clr-white);
  border-radius: 10px;
}

.popover-profile.active {
  opacity: 1;
  visibility: visible;
}

.popover-profile li {
  list-style: none;
  margin-top: 2px;
}

.popover-profile li .logout {
  padding: 10px 20px;
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-lightblue), 1);
  color: #fff;
  color: var(--clr-white);
}

.popover-profile li .logout:hover {
  background-color: rgba(99, 102, 241, 1);
  background-color: rgba(var(--clr-blue), 1);
  color: #fff;
  color: var(--clr-white);
}

.popover-profile li button {
  display: flex;
  width: 100%;
  grid-gap: 10px;
  gap: 10px;
  padding: 10px 20px;
  border-radius: 5px;
  align-items: center;
  justify-content: space-evenly;
}

.popover-profile li button:hover {
  cursor: pointer;
  background-color: rgba(99, 102, 241, 20%);
  background-color: rgba(var(--clr-lightblue), 20%);
  color: rgba(99, 102, 241, 1);
  color: rgba(var(--clr-lightblue), 1);
}

@media (min-width: 942px) {
  .dashboard-wrapper {
    width: 100%;
    display: flex;
  }

  /* ================= Header ================= */

  .dashboard-header {
    background-color: #FFF;
    width: 100vw;
    height: 75px;
    height: var(--header-h);
    position: fixed;
    top: 0;
    z-index: 10;
  }

  .header-menu {
    display: grid;
    grid-template-columns: auto 1fr;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  .header-menu-item-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header-menu-item.menu-brand {
    width: 270px;
    width: var(--aside-w);
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;
    text-align: center;
  }

  .header-menu-item.menu-brand .brand {
    margin: auto;
  }

  .header-menu-item.menu-profile {
    justify-self: flex-end;
    margin-right: 50px;
  }

  .search-bar input {
    padding: 0 10px;
    width: 300px;
    font-size: 18px;
    background-color: transparent;
    outline: none;
    border: none;
  }

  /* ================= Aside ================= */

  .aside-container {
    min-width: 270px;
    min-width: var(--aside-w);
    height: 100vh;
    height: var(--aside-h);
    margin-left: calc(0px - 270px);
    margin-left: calc(0px - var(--aside-w));
    background-color: white;
    box-sizing: border-box;
    padding: 0 20px;
    /* transition-delay: .1s; */
    transition-duration: .4s;
    transition-property: all;
    overflow-y: scroll;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }

  .aside-container.active {
    width: 270px;
    width: var(--aside-w);
    height: 100vh;
    height: var(--aside-h);
    margin-left: 0;
    background-color: white;
    box-sizing: border-box;
    padding: 0 20px;
  }
}

@media (max-width:941px) {
  .dashboard-wrapper {
    width: 100%;
    display: flex;
  }

  /* ================= Header ================= */

  .dashboard-header {
    background-color: #FFF;
    height: 75px;
    height: var(--header-h);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
  }

  .header-menu {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header-menu-item.menu-brand {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;
  }

  .header-menu-item.menu-profile {
    margin-right: 10px;
  }

  .search-bar input {
    padding: 0 10px;
    width: 180px;
    font-size: 18px;
    background-color: transparent;
    outline: none;
    border: none;
  }

  /* ================= Aside ================= */

  .aside-container {
    position: fixed;
    z-index: 20;
    min-width: 270px;
    min-width: var(--aside-w);
    height: 100vh;
    height: var(--aside-h);
    margin-left: 0;
    background-color: white;
    box-sizing: border-box;
    padding: 0 20px;
    padding-bottom: 20px;
    /* transition-delay: .1s; */
    transition-duration: .4s;
    transition-property: all;
    overflow-y: scroll;
  }

  .aside-container.active {
    width: 270px;
    width: var(--aside-w);
    height: 100vh;
    height: var(--aside-h);
    margin-left: calc(0px - 270px);
    margin-left: calc(0px - var(--aside-w));
    background-color: white;
    box-sizing: border-box;
    padding: 0 20px;
  }

  .aside-dark {
    opacity: 0;
    visibility: hidden;
  }

  .aside-dark.active {
    opacity: 1;
    visibility: visible;
    background-color: rgba(0, 0, 0, 50%);
    position: fixed;
    overflow: hidden;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
  }
}

@media (max-width: 321px) {
  .search-bar input {
    width: 120px;
  }
}

